/* General Home Layout */
.home-container {
  min-width: 100%;
  display: flex;
  align-items: center;
  background-color: #050505;
  padding-bottom: 60px;
  padding-right: 80px;
  padding-left: 80px;
  box-sizing: border-box;
  flex-direction: column;
}

.home-banner {
  min-width: 100%;
  display: flex;
  align-items: center;
  padding-top: 20px;
  padding-right: 80px;
  padding-left: 80px;
  position: relative;
  background-color: #050505;
}

.home-banner img {
  width: 100%;
  height: auto;
  object-fit: cover; 
}

.home-banner-img.mobile {
  display: none;
}

.home-banner .home-banner-text {
  position: absolute;
  font-family: "Proxima Nova";
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  letter-spacing: normal;
  line-height: 16px;
  color: #1C1C1C;
  left: 30%;
  bottom: 24%;
  width: 40%;
  text-transform: none;
  text-align: center;
}

.home-banner-button-row {
  position: absolute;
  left: 50%;
  bottom: 10%;
  transform: translateX(-50%);
  display:inline-flex;
  justify-content: center;
  align-items: center;
  width: 90%;
  max-width: 600px;
  padding: 0 10px;
  gap: 20px;
}

.home-banner .home-register,
.home-banner .home-learn-more {
  width: auto;
  height: 64px;
  padding: 20px 40px;
  font-family: "Proxima Nova";
  font-size: 16px;
  font-weight: 600;
  line-height: 16px;
  letter-spacing: 3.2px;
  text-transform: uppercase;
  border-radius: 16px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.home-banner .home-register {
  background-color: #1C1C1C;
  color: #F2F2F2;
  border: none;
}
.home-banner .home-register:hover {
  background-color: #0E0E0E;
}
.home-banner .home-register:active {
  background-color: #9ECB3B;
  color: #050505;
}

.home-banner .home-learn-more {
  background-color: transparent;
  color: #050505;
  border: 1.8px solid #050505;
  display: flex;
  align-items: center;
  justify-content: center;
}

.home-banner .home-learn-more:hover {
  background: rgba(28, 28, 28, 0.20);
}
.home-banner .home-learn-more:active {
  background-color: #9ECB3B;
  color: #050505;
  border-color: #9ECB3B;
}

.home-banner .home-banner-button-link {
  text-decoration: none;
}

.home {
  width: 100%;
  padding-top: 20px;
  padding-bottom: 40px;
  padding-left: 80px;
  padding-right: 80px;
  background-color: #050505;
  color: #F2F2F2;
  align-items: start;
}

.home-inner {
  display: grid;
  grid-template-columns: 350px auto; 
  grid-template-rows: auto; 
  gap: 20px;
  width: 100%;
  color: #F2F2F2;
  background-color: #050505;
}

/*** News Section ***/
.news-section {
  grid-column: 1/2;
  grid-row: 1/4;
  display: flex;
  flex-direction: column;
  align-self: start;
  justify-content: flex-start;
  background-color: #0E0E0E;
  border-radius: 20px;
  padding: 10px 20px 20px 20px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.5);
  max-height: fit-content;
}

.news-section h2 {
  font-family: "Proxima Nova";
  color: #F2F2F2;
  font-size: 32px;
  font-style: normal;
  font-weight: 700;
  line-height: 32px;
  letter-spacing: 6.4px;
  text-transform: uppercase;
  padding-bottom: 10px;
}

.news-item {
  flex-direction: column;
  background-color: #1C1C1C;
  display: flex;
  border-radius: 16px;
  padding: 0px 20px;
  margin-bottom: 8px;
  border: 1px solid #333;
}

.news-item h3 {
  font-family: "Proxima Nova";
  font-size: 20px;
  font-style: normal;
  font-weight: 600;
  line-height: 24px;
  letter-spacing: 0px;
  color: #F2F2F2;
  margin-bottom: 0px;
  text-transform: capitalize;
}

.news-item .news-date {
  color: #F2F2F2;
  font-size: 16px;
  font-family: "Proxima Nova";
  font-style: normal;
  font-weight: 300;
  line-height: 16px;
  margin-top: 8px;
  margin-bottom: 8px;
}

.news-item p {
  font-size: 16px;
  font-family: "Proxima Nova";
  font-style: normal;
  font-weight: 300;
  line-height: 18px;
  color: #F2F2F2;
}

.news-item:last-child {
  border: 0px;
}

.news-item:last-child p {
  margin-top: 0px;
  margin-bottom: 20px ;
}

.news-item.content {
  white-space: pre-line;
}

.youtube-link {
  color: #F2F2F2;
  text-decoration: underline;
}

.youtube-link:hover {
  color: #B4E05F;
  text-decoration: underline;
}

/* Schedule Dropdown Section */
.schedule-dropdown {
  grid-column: 2 / 3;
  grid-row: 1/2;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #0E0E0E;
  padding: 5px 20px;
  border-radius: 20px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.5);
}

.schedule-dropdown h3 {
  font-family: "Proxima Nova";
  font-size: 32px;
  font-style: normal;
  font-weight: 700;
  line-height: 32px;
  letter-spacing: 6.4px;
  color: #F2F2F2;
}

/* Container */
.dropdown-container {
  position: relative;
  display: inline-block;
  min-width: 240px;
  height: fit-content;
}

/* Button */
.dropdown-button {
  min-width: fit-content;
  padding: 20px 20px;
  font-family: "Proxima Nova";
  font-size: 16px;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 3.2px;
  line-height: 16px;
  border-radius: 16px;
  justify-content: space-between;
  align-items: center;
  color: #F2F2F2;
  border: 1px solid #333;
  background-color: #1C1C1C;
  display: flex;
  cursor: pointer;
  transition: background-color 0.3s ease;
  white-space: nowrap; 
}

.dropdown-button .month-title {
  display: flex;
  justify-self: flex-start;
  font-family: "Proxima Nova";
  font-size: 16px;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 3.2px;
  line-height: 16px;
}

.dropdown-button:hover {
  background-color: #161616;
  font-family: "Proxima Nova";
  font-size: 16px;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 3.2px;
  line-height: 16px;
  justify-content: space-between;
  border-radius: 16px;
}

.dropdown-button.active {
  font-family: "Proxima Nova";
  font-size: 16px;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 3.2px;
  line-height: 16px;
  background-color: #9ECB3B;
  color: #161616;
  border: 1px solid #9ECB3B;
  justify-content: space-between;
  border-radius: 16px;
}

.dropdown-button .dropdown-arrow {
  margin-left: 2px;
  padding-bottom: 4px;
  font-size: 20px;
  color: #9ECB3B;
  transition: transform 0.3s ease; 
  display: flex;
  justify-content: flex-end;
  max-width: fit-content;
}

.dropdown-button .dropdown-arrow.open {
  color: #161616;
  transform: rotate(180deg); 
}

.downarrow-icon {
  max-width: 18px;
  max-height: 18px;
  margin-top: 2px;
}

/* Dropdown Menu */
.dropdown-menu {
  position: absolute;
  top: calc(100% + 15px);
  left: 0;
  width: 100%; 
  background-color: #1C1C1C;
  color: #F2F2F2;
  list-style: none;
  padding: 0px 0px;
  margin: 0;
  overflow: hidden;
  animation: dropdown-slide-down 0.3s ease forwards;
  z-index: 1000;
  border-radius: 0 0 2px 2px;
  border-top: 8px solid #9ECB3B;
  box-shadow: 0px 4px 8px 0px rgba(0, 0, 0, 0.25);
}

/* Menu Items */
.dropdown-item {
  padding: 15px 20px;
  font-size: 16px;
  font-family: "Proxima Nova";
  font-weight: 500;
  letter-spacing: 3.2px;
  background-color: transparent;
  cursor: pointer;
  transition: background-color 0.2s ease, padding-left 0.3s ease;
  border: 1px solid rgba(255, 255, 255, 0.1); 
  text-transform: uppercase;
}

.dropdown-item:hover {
  background-color: #161616;
}

.dropdown-item:active {
  background-color: #9ECB3B;
  color: #161616;
}

/*** Schedule Section ***/
.schedule-section {
  display: flex;
  flex-direction: column;
  grid-column: 2/3;
  grid-row: 2/3;
  width: 100%;
  align-items: stretch;
  font-family: "Proxima Nova";
}

.schedule-item {
  display: flex;
  flex-direction: column;
  background: #0E0E0E;
  border-radius: 20px;
  padding: 0px 20px;
  margin-bottom: 20px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
  transition: transform 0.2s, box-shadow 0.2s;
}

.schedule-item-content {
  display: flex;
  flex-direction: column;
  flex-grow: 1;
  padding-bottom: 15px;
}

.row-top {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 10px;
}

.sport-title-wrapper {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  gap: 16px;
  padding-top: 16px;
  padding-bottom: 4px;
}


.sport-icon {
  max-width: 32px;
  max-height: 32px;
}

.sport-title {
  color: #F2F2F2;
  font-family: "Proxima Nova";
  font-size: 20px;
  font-weight: 600;
  font-style: normal;
  text-transform: uppercase;
  line-height: 24px;
  letter-spacing: 4px;
}

.schedule-date-mobile {
  display: none;
}

.schedule-date {
  color: #F2F2F2;
  font-family: "Proxima Nova";
  font-size: 16px;
  font-weight: 600;
  line-height: 16px;
  letter-spacing: 4px;
  text-transform: uppercase;
  display: inline-flex;
  padding: 20px;
  justify-content: flex-end;
  gap: 8px;
  border-radius: 16px;
  background-color: #161616;
}

.row-bottom {
  display: flex;
  gap: 0px 8px;
  align-items: center;
}

.teams-info {
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-direction: column;
  gap: 8px; 
  flex-grow: 2;
}

.team-bar {
  display: flex;
  align-self: stretch;
  border-radius: 16px;
  flex-direction: row;
  align-items: center;
  gap: 8px; 
  flex-grow: 1;
}

.team-bar .team-loser {
  background-color: #161616;
  color: #F2F2F2;
}

.team-score-row {
  display: flex;
  align-items: flex-end;
  flex-direction: row;
  align-items: center;
  margin: 0;
  gap: 8px;
  min-height: 100%;
}

.team-score {
  border-radius: 16px;
  border: 1px solid #333;
  background-color: #1C1C1C;
  font-family: "Manuka";
  font-size: 48px;
  margin: 0px;
  padding: 10px;
  font-style: normal;
  font-weight: 700;
  text-transform: uppercase;
  text-align: center;
  color: #F2F2F2;
  letter-spacing: 0.8px;
  min-width: 70px;
  min-height: 100%;
  max-height: 100%;
}

.team-row {
  display: flex;
  align-self: stretch;
  align-items: flex-end;
  border-radius: 16px;
  border: 1px solid #333;
  background-color: #1C1C1C;
  color: #F2F2F2;
  padding: 0px 20px;
  flex-direction: row;
  align-items: center;
  flex-grow: 1;
}

.team-icon {
  max-width: 35px; 
  max-height: 35px;
  padding-right: 12px;
}

.team-details {
  margin: 12px 0px -8px 0px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 2px;
  flex-grow: 1;
}

.team-info-row {
  display: flex;
  align-items: center;
  vertical-align: center;
  gap: 4px;  
}

.team-name {
  font-family: "Proxima Nova";
  font-size: 20px;
  font-style: normal;
  font-weight: 600;
  color: #F2F2F2;
  text-transform: capitalize;
  margin: 0;  
  line-height: 20px;
}

.team-abbrev {
  font-family: "Proxima Nova";
  color: #D2D2D2;
  font-size: 16px;
  font-weight: 300;
  margin: 0;
}

.team-record {
  font-family: "Proxima Nova";
  font-size: 16px;
  color: #F2F2F2;
  margin-top: 4px; 
  margin-bottom: 20px;
  font-weight: 400;
}

.play-button-mobile {
  display: none;
}

.play-button {
  width: 145px;
  max-width: 145px;
  height: 100%;
  font-size: 40px;
  background-color: #1C1C1C;
  color: #9ECB3B;
  padding-left: 45px;
  border: 1px solid #333;
  border-radius: 18px;
  cursor: pointer;
  transition: background-color 0.2s, transform 0.2s;
  display: flex;
  justify-content: center;
  align-items: center;
  align-self: center; /* Will override on mobile */
}

.play-icon {
  max-width: 35px;
  max-height: 45px;
}

.play-button:hover {
  background-color: #161616;
  border-radius: 18px;
}

.play-button:active {
  background-color: #9ECB3B;
  color: #1C1C1C;
  border-radius: 18px;
}

/*** League Leaders Section ***/
.league-section {
  grid-column: 2 / 3; 
  grid-row: 3;
  margin-top: 0;
}

.league-header {
  display: flex; 
  justify-content: space-between; 
  align-items: center; 
  background-color: #0E0E0E;
  border-radius: 20px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.5);
  padding: 20px;
}

.league-header h3 {
  font-family: "Proxima Nova";
  font-size: 32px;
  font-style: normal;
  font-weight: 700;
  line-height: 32px;
  letter-spacing: 6.4px;
  color: #F2F2F2;
  margin: 0; 
}

.league-header .view-all {
  display: flex;
  justify-content: center; 
  align-items: center;
  padding: 24px 28px;
  width: fit-content;
  gap: 4px;
  border-radius: 16px;
  border: 1px solid #333;
  background: #1C1C1C;
  font-family: "Proxima Nova";
  font-size: 16px;
  font-style: normal;
  font-weight: 600;
  line-height: 16px;
  letter-spacing: 3.2px;
  color: #F2F2F2;
  text-transform: uppercase;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.league-section .view-all:hover {
  background-color: #161616;
  border-radius: 16px;
}

.league-section .view-all:active {
  background-color: #9ECB3B;
  color: #161616;
  border: 1px solid #9ECB3B;
  border-radius: 16px;
}

.league-section .view-all-link {
  text-decoration: none; 
}

.current-record {
  background-color: #0E0E0E;
  border-radius: 20px;
  padding: 20px;
  margin: 20px 0px;
}

.current-teams .team-row:last-child {
  margin-top: 8px;
}

.current-teams .losing-team {
  background-color: #161616; 
}

.leaderboard {
  display: grid;
  grid-template-columns: repeat(2, 1fr); 
  gap: 20px; 
  margin: 0 auto;  
}

.league-item {
  background-color: #0E0E0E;
  color: #F2F2F2;
  border-radius: 20px;
  display: flex;
  flex-direction: column;
  width: 100%;
  box-sizing: border-box;
  padding: 20px;
}

.league-item .row-top {
  display: flex;
  align-items: center;
  padding: 0px 0px;
}

.player-list1 {
  display: flex;
  align-items: start;
  flex-direction: column;
  font-family: "Proxima Nova";
}

.player-row {
  display: flex;
  justify-content: flex-start; 
  align-items: center;
  padding: 4px 18px;
  border-radius: 16px;
  background-color: #1C1C1C;
  width: 100%;
  box-sizing: border-box;
  border: 1px solid #333;
  gap: 16px;
  margin: 4px 0px;  
}

.player-row .rank {
  font-family: "Proxima Nova";
  font-size: 16px;
  font-weight: 600;
  max-width: 24px;
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
}

.player-info {
  display: flex;
  flex-direction: column;
  flex: 1;
  padding-top: 8px;
  padding-bottom: 8px;
  gap: 8px;
}

.player-info .top-row {
  display: flex;
  flex-wrap: wrap; 
  gap: 4px; 
  margin-top: 4px;
}

.player-name {
  font-family: "Proxima Nova";
  font-size: 20px;
  font-weight: 600;
  color: #F2F2F2;
}

.player-team {
  font-family: "Proxima Nova";
  font-size: 16px;
  font-weight: 400;
  color: #D2D2D2;
}

.player-stat {
  font-family: "Proxima Nova";
  font-size: 16px;
  font-style: normal;
  font-weight: 300;
  line-height: 16px;
  color: #F2F2F2;
}

.see-all-wrapper {
  display: flex;
  justify-content: center;
  margin-top: auto;
}

.see-all-button {
  display: flex;
  align-items: center;
  font-family: "Proxima Nova";
  font-size: 20px;
  font-style: normal;
  font-weight: 600;
  line-height: 24px; 
  letter-spacing: 4px;
  text-transform: uppercase;
  cursor: pointer;
  color: #9ECB3B;
  background-color: transparent;
  border: none;
  transition: color 0.3s ease;
  gap: 0;
  margin-bottom: -10px;
}

.arrow-icon {
  max-width: 20px;
  max-height: 20px;
}

.see-all-button:hover {  
  font-family: "Proxima Nova";
  font-size: 20px;
  font-style: normal;
  font-weight: 600;
  line-height: 24px; 
  letter-spacing: 4px;
  text-transform: uppercase;
  cursor: pointer;
  color: #C7E584;
  background-color: transparent;
  gap: 0;
}

.see-all-button:active {
  font-family: "Proxima Nova";
  font-size: 20px;
  font-style: normal;
  font-weight: 600;
  line-height: 24px;
  letter-spacing: 4px;
  text-transform: uppercase;
  cursor: pointer;
  color: #333333;
  background-color: transparent;
  gap: 0;
}

@media screen and (max-width: 1350px) {
  .home-banner .home-banner-text {
    display: none;
  }
}


/* Tablet View (up to 1200px) */
@media screen and (max-width: 1200px) {
  .home-container {
    padding-left: 60px;
    padding-right: 60px;
  }

  .home-banner {
    padding-left: 40px;
    padding-right: 40px;
  }

  .home-banner .home-banner-text {
    display: none;
  }

  .home-banner-button-row {
    left: 50%;
    gap: 24px;
  }
  .home-banner .home-register,
  .home-banner .home-learn-more {
    display: none;
  }

  .home {
    padding: 20px 40px;
  }

  .home-inner {
    display: flex;
    flex-direction: column;
  }

  .schedule-dropdown {
    width: stretch;
  }

  .news-section {
    display: none;
  }

  .team-score-row {
    overflow-x: scroll;
    scrollbar-width: none;
  }
  
  .play-button {
    min-height: 100%;
    height: 152px;
  }
}

@media screen and (max-width: 900px) {
  .home {
    border-top-right-radius: 30px;
    border-top-left-radius: 30px;
    margin-top: -30px;
    position: relative;
    z-index: 10;
    background-color: #050505;
    overflow: hidden;
    padding: 0px;
    min-width: 0;
    width: 100%;
    padding-bottom: 40px;
    margin-bottom: 40px;
  }

  .home-inner {
    position: relative;
    border-top-right-radius: 30px;
    border-top-left-radius: 30px;
    display: flex;
    padding-top: 10px;
    overflow: hidden;
    flex-direction: column;
    flex-wrap: wrap;
    width: 100%;
    min-width: stretch;
    box-sizing: border-box;
  }

  .home-container {
    padding: 0px;
    min-width: 0;
    width: 100%;
  }

  .home-banner {
    padding: 0px;
    z-index: 1;
    position: relative;
  }

    .home-banner-img.desktop {
      display: none;
    }
    .home-banner-img.mobile {
      display: flex;
    }

    .home-banner-button-row {
      left: 35%;
      gap: 24px;
    } 

    .home-banner-button-row {
      display: none;
    }

  .schedule-dropdown {
    display: flex;
    flex-direction: column;
    background-color: transparent;
    width: 100%;
  }

  .schedule-dropdown h3 {
    align-self: flex-start;
  }
  
  .schedule-section {
    display: flex;
    box-sizing: border-box;
    padding: 0px 16px;
  }

  .schedule-item {
    background-color: #0E0E0E;
  }

  .schedule-item-content {
    display: flex;
    flex-direction: column;
  }

  .sport-icon {
    width: 40px;
    height: 40px;
  }

  .sport-title {
    margin: 0px;
    font-family: "Proxima Nova";
    font-size: 16px;
    font-style: normal;
    font-weight: 600;
    line-height: 24px; 
    letter-spacing: 4px;
